import { StrictMode } from 'react';
import { createRoot } from 'react-dom/client';
import { decompress } from 'lz-string';
import localforage from 'localforage';

import type { GameModel } from './models/game';
import type { OptionsModel } from './models/options';

import { Main } from './components/main/main';

import './index.scss';

localforage.getItem<GameModel>('skirmish-game').then(game => {
	if (!game) {
		const str = window.localStorage.getItem('skirmish-game');
		if (str) {
			const gameJSON = decompress(str);
			if (gameJSON) {
				game = JSON.parse(gameJSON) as GameModel;
			}
		}
	}
	localforage.getItem<OptionsModel>('skirmish-options').then(options => {
		if (!options) {
			const str = window.localStorage.getItem('skirmish-options');
			if (str) {
				const optionsJSON = decompress(str);
				if (optionsJSON) {
					options = JSON.parse(optionsJSON) as OptionsModel;
				}
			}
		}
		if (!options) {
			options = {
				developer: false,
				soundEffectsVolume: 0.5
			};
		}
		const container = document.getElementById('root');
		if (container) {
			const root = createRoot(container);
			root.render(
				<StrictMode>
					<Main game={game} options={options} />
				</StrictMode>
			);
		}
	});
});
